/* Akk Hustlers Premium Dark Theme */
:root {
  --primary-dark: #0D0D0D;       /* Rich black background */
  --secondary-dark: #1A1A1A;     /* Dark charcoal */
  --accent-gold: #FFD700;        /* Vibrant gold */
  --accent-gold-dark: #D4AF37;   /* Darker gold for hover states */
  --accent-teal: #00CED1;        /* Complementary teal */
  --text-primary: #F5F5F5;       /* Off-white text */
  --text-secondary: #B3B3B3;     /* Light gray text */
  --border-color: #333333;       /* Dark gray borders */
  --card-bg: #1E1E1E;            /* Card background */
}

/* Base Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

html {
  scroll-behavior: smooth;
  background-color: var(--primary-dark);
}

body {
  color: var(--text-primary);
  background-color: var(--primary-dark);
}

a {
  text-decoration: none;
  color: var(--accent-gold);
  transition: color 0.3s ease;
}

a:hover {
  color: var(--accent-teal);
}

p {
  color: var(--text-secondary);
  line-height: 1.6;
}
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100px;
  width: 100%;
 background-color: black;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  position: fixed;
  top: 0;
  z-index: 10;
  padding: 0 20px;
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  border-bottom: 1px solid rgba(255, 215, 0, 0.1);
}

.headImgDiv {
  display: flex;
  align-items: center;
}

.headImgDiv img {
  height: 100px;
  width: auto;
  cursor: pointer;
  transition: transform 0.3s ease;
}
.headImgDiv img:hover {
  transform: scale(1.05);
}

.navTools {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 30px;
}

.navTools a, .navTool a {
  font-weight: 500;
  text-decoration: none;
  color: var(--text-primary);
  text-decoration: none;
  position: relative;
  padding: 8px 0;
}

.navTools a::after, .navTool a::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background-color: var(--accent-gold);
  transition: width 0.3s ease;
}

.navTools a:hover::after, .navTool a:hover::after {
  width: 100%;
}

.navTools a:hover, .navTool a:hover {
  color: var(--accent-gold);
}

.barMenu {
  display: none;
}

/* Mobile menu */
.navToolMedia {
  width: 100%;
  background-color: black;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  transition: 0.4s ease;
  padding: 1rem 0;
}
.navToolMedia a {
  font-size: 1.2rem;
  width: 100%;
  text-align: center;
  padding: 10px 0;
  border-bottom: 1px solid rgba(255, 215, 0, 0.2);
  font-size: 1.2rem;
  color: var(--accent-gold);
}
.navToolMedia a:hover {
  background-color: rgba(255, 215, 0, 0.1);
  color: var(--text-primary);
}

.noneDisplay {
  position: absolute;
  top: -500px;
  left: 0;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
}

.yesDisplay {
  position: absolute;
  top: 100px;
  left: 0;
  opacity: 1;
  visibility: visible;
  transition: all 0.3s ease;
  z-index: 5;
}


.heroSection {
  margin-top: 100px;
  height: auto;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px 20px;
  background: linear-gradient(
    135deg, 
    var(--primary-dark) 0%, 
    #121212 100%
  );
  text-align: center;
  overflow: hidden;
}
.heroSection::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(
    circle at 70% 30%,
    rgba(255, 215, 0, 0.05) 0%,
    transparent 70%
  );
  pointer-events: none;
}

.container {
  width: 100%;
  max-width: 1000px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.container h1 {
  font-size: 2.5rem;
  margin-bottom: 20px;
  color: var(--text-primary) !important;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.3);
   line-height: 1.2;
}
.container h1 span {
  color: var(--accent-gold) !important;
}

.container p {
  font-size: 1.2rem;
  color: var(--text-secondary) !important;
  max-width: 700px;
  margin-bottom: 20px;
}

.heroAnchor {
  margin-top: 20px;
  height: 50px;
  width: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
   background: linear-gradient(
    135deg,
    var(--accent-gold) 0%,
    var(--accent-gold-dark) 100%
  );
  border-radius: 50px;
  transition: all 0.3s;
  cursor: pointer;
  box-shadow: 0 4px 15px rgba(255, 215, 0, 0.3);
  
}
.heroAnchor::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    135deg,
    var(--accent-teal) 0%,
    #008B8B 100%
  );
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: -1;
  border-radius: 50px;
}

.heroAnchor:hover::before {
  opacity: 1;
}
 
.heroAnchor:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 25px rgba(255, 215, 0, 0.4);
}

.heroAnchor a {
  font-weight: 600;
  font-size: 1rem;
  color: var(--primary-dark) !important;
  text-decoration: none;
}

.allTools{
  background-color: var(--secondary-dark);
}
.popular {
  height: 200px;
  margin-top: 60px;
  margin-bottom: 60px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  padding-top: 20px;
  
}

.popular p {
  font-size: 1.2rem;
  color: var(--text-secondary) !important;
  margin-bottom: 10px;
  align-self: center;
}

.popular h1{
  color: var(--text-primary) !important;
}


.popular input {
  width: 50%;
  height: 40px;
  padding-left: 15px;
  background-color: var(--card-bg);
  color: var(--text-primary);
  border: 1px solid #00ffff;
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.3s ease;
}
.popular input:focus {
  outline: none;
  border-color: var(--accent-gold);
  box-shadow: 0 0 0 2px rgba(255, 215, 0, 0.2);
}

.completeTools {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  gap: 30px;
}

.topServices {
  height: 380px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  background-color: var(--card-bg);
  border-radius: 16px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.2);
  transition: all 0.4s ease;
  overflow: hidden;
  position: relative;
  border: 1px solid lightgreen;
}

.topServices::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 5px;
  background: linear-gradient(
    90deg,
    var(--accent-gold) 0%,
    var(--accent-teal) 100%
  );
}

.topServices:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.3);
  border-color: rgba(255, 215, 0, 0.3);
}

.first {
  width: 80%;
}

.second {
  width: 47%;
}

.third {
  width: 30%;
}

.topServices:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
}

.services {
  width: 80%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.services h1 {
  font-size: 1.5rem;
  color: var(--text-primary) !important;
}

.services p {
  color: var(--text-secondary) !important;
}

.services a {
  height: 45px;
  width: 160px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  color: var(--accent-gold);
  border: 2px solid var(--accent-gold);
  border-radius: 50px;
  font-weight: 600;
  transition: all 0.3s ease;
}
.services a:hover {
  background-color: var(--accent-gold);
  color: var(--primary-dark);
  transform: translateY(-3px);
  box-shadow: 0 5px 15px rgba(255, 215, 0, 0.3);
}

.lucideIconServices {
  transform: scale(1.5);
}

#howItWorks {
  padding: 100px 20px;
  background-color: var(--primary-dark);
}

.firstWork {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
   margin-bottom: 60px;
}

.firstWork h1 {
  font-size: 2rem;
  color: var(--text-primary) !important;
  margin-bottom: 15px;
  position: relative;
}
.firstWork h1::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 3px;
  background-color: var(--accent-teal) !important;;
}

.fir {
  width: 25%;
}

.digit {
  height: 60px;
  width: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(
    135deg,
    var(--accent-teal) 0%,
    #008B8B 100%
  );
  border-radius: 50%;
   color: var(--primary-dark);
   margin-bottom: 10px;
}

.digit h1 {
  font-size: 1.5rem;
}

.flex {
  margin-top: 20px;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.jabay {
  height: 95%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  padding: 30px;
  background-color: var(--card-bg);
  border-radius: 16px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
  border: 1px solid rgba(0, 206, 209, 0.1);
  margin: 20px;
}
  
.jabay:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.3);
}

.jabay h1 {
  font-size: larger;
}

#benifits {
  height: 350px;
  margin-top: 40px;
   background-color: var(--secondary-dark);
  display: flex;
  justify-content: center;
  align-items: center;
}

.inner {
  height: 90%;
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.lightning {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.flexi {
  display: flex;
  margin: 20px 0px 20px 0px;
  position: relative;
  right: 20px;
}

.flexi h1 {
  font-size: 1.2rem;
}

.sub {
  width: 19%;
  height: 100%;
}

#testimonials {
  height: 500px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
   background-color: var(--primary-dark);
}

#testimonials h1 {
  font-size: 2.5rem;
  color: var(--text-primary);
  text-align: center;
  margin-bottom: 15px;
  position: relative;
}

#testimonials h1::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 3px;
  background-color: var(--accent-gold);
}
#testimonials p {
  color: var(--text-secondary);
  text-align: center;
  max-width: 700px;
  margin: 0 auto 50px;
}

.mini {
  ime-mode: 100%;
  display: flex;
  justify-content: space-evenly;
}

.monial {
  height: 250px;
  width: 350px;
  border-radius: 20px;
  background-color: var(--card-bg);
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.2);
  margin: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease;
  border: 1px solid #00ffff;
}
.monial:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.3);
}

.min {
  height: 80%;
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.min i {
  color: var(--text-secondary);
  font-style: italic;
  font-size: 1.1rem;
  line-height: 1.7;
}
.in {
  display: flex;
  width: 100%;
}

.out {
  margin-left: 30px;
}

.out p {
  margin-top: 7px;
  font-size: 0.9rem;
}

.out h3 {
  font-size: 1rem;
  color: var(--text-primary) !important;
}

.outerWorkout {
  padding: 100px 20px;
  background: linear-gradient(
    135deg,
    var(--primary-dark) 0%,
    #121212 100%
  );
  position: relative;
  overflow: hidden;
}

.outerWorkout::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(
    circle at 30% 70%,
    rgba(0, 206, 209, 0.1) 0%,
    transparent 70%
  );
  pointer-events: none;
}

.workout {
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
}
.workout h1 {
  font-size: 2.5rem;
  color: var(--text-primary);
  margin-bottom: 20px;
  position: relative;
  display: inline-block;
}

.workout h1::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 3px;
  background-color: var(--accent-gold);
}

.workout h3 {
  font-size: 2rem;
  color: var(--text-primary);
  margin: 10px;
  position: relative;
  display: inline-block;
}

.workout h3::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 3px;
  background-color: var(--accent-gold);
}

.workout p {
  color: var(--text-secondary);
  font-size: 1.2rem;
  margin-bottom: 40px;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.workout a {
  height: 60px;
  width: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(
    135deg,
    var(--accent-gold) 0%,
    var(--accent-gold-dark) 100%
  );
  color: var(--primary-dark);
  font-weight: 600;
  border-radius: 50px;
  transition: all 0.3s ease;
  margin: 0 auto;
  box-shadow: 0 4px 15px rgba(255, 215, 0, 0.3);
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.workout a::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    135deg,
    var(--accent-teal) 0%,
    #008B8B 100%
  );
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: -1;
}

.workout a:hover::before {
  opacity: 1;
}

.workout a:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 25px rgba(255, 215, 0, 0.4);
}
/* Footer */
.footer {
  padding: 80px 20px 40px;
  background-color: var(--primary-dark);
  border-top: 1px solid rgba(255, 215, 0, 0.1);
}

.firstfooter {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding-bottom: 60px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.innerone {
  width: 40%;
}

.innerone h3 {
  font-size: 2rem;
  color: var(--accent-gold);
  margin-bottom: 20px;
  font-weight: 700;
}

.innerone i {
  color: var(--text-secondary);
  font-size: 1.1rem;
  line-height: 1.7;
  margin-bottom: 30px;
  display: block;
}

.footertag {
  display: flex;
  gap: 20px;
}

.footertag a {
  color: var(--text-secondary);
  font-size: 1.3rem;
  transition: all 0.3s ease;
}

.footertag a:hover {
  color: var(--accent-gold);
  transform: translateY(-3px);
}

.innertwo {
  width: 50%;
  display: flex;
  flex-direction: column;
}

.innertwo h3 {
  font-size: 1.5rem;
  color: var(--accent-teal);
  margin-bottom: 25px;
  font-weight: 600;
}

.innertwo a {
  color: var(--text-secondary);
  margin-bottom: 15px;
  font-size: 1.1rem;
  transition: all 0.3s ease;
}

.innertwo a:hover {
  color: var(--accent-gold);
  padding-left: 10px;
}

.copyright {
  max-width: 1200px;
  margin: 40px auto 0;
  text-align: center;
  color: var(--text-secondary);
  font-size: 0.9rem;
  padding-top: 20px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.innerCounter {
  position: relative;
  top: 100px;
  margin-bottom: 100px;
  margin-top: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
   background-color: var(--primary-dark);
}

.paracounter {
  text-align: center;
  margin-bottom: 40px;
}

.paracounter h1 {
  font-size: 3rem;
  font-weight: 700;
  margin-bottom: 10px;
  color: var(--accent-gold);
}

.paracounter p {
  font-size: 1.2rem;
   color: var(--text-secondary);
}

.inputCounter {
  width: 90%;
  height: auto;
  padding: 30px;
  border-radius: 12px;
  background: var(--card-bg);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 215, 0, 0.1);
  display: flex;
  flex-direction: column;
  gap: 30px;
}
.inputCounter textarea {
  width: 100%;
  min-height: 200px;
  padding: 15px 20px;
  border-radius: 8px;
  font-size: 16px;
  line-height: 1.6;
  resize: vertical;
  background-color: var(--secondary-dark);
  border: 1px solid var(--border-color);
  color: var(--text-primary);
  transition: border-color 0.3s, box-shadow 0.3s;
}

.inputCounter textarea:focus {
  outline: none;
  border-color: var(--accent-teal);
  box-shadow: 0 0 0 3px rgba(0, 206, 209, 0.2);
}

.borderCounter {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 20px;
}

.counting {
  border-radius: 8px;
  padding: 20px 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 180px;
  transition: transform 0.3s, box-shadow 0.3s;
  text-align: center;
  background-color: var(--secondary-dark);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  border: 1px solid rgba(255, 215, 0, 0.1);
  color: var(--text-primary);
}

.counting:hover {
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
  border-color: rgba(255, 215, 0, 0.3);
}

.copyCounter {
  display: flex;
  justify-content: center;
  gap: 15px;
  flex-wrap: wrap;
}

.clearBtn,
.copyBtn {
  padding: 12px 24px;
  border-radius: 6px;
  font-size: 16px;
  display: flex;
  align-items: center;
  gap: 10px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.05);
}

.clearBtn {
  background-color: #f8f9fa;
  color: #2c3e50;
  border: 1px solid #dcdcdc;
}

.clearBtn:hover {
  background-color: rgba(255, 215, 0, 0.1);
  border-color: var(--accent-gold);
  color: white;
}

.copyBtn {
  background: linear-gradient(
    135deg,
    var(--accent-gold) 0%,
    var(--accent-gold-dark) 100%
  );
  color: var(--primary-dark);
  box-shadow: 0 5px 15px rgba(255, 215, 0, 0.3);
  border: none;
}

.copyBtn:hover {
  background: linear-gradient(
    135deg,
    var(--accent-teal) 0%,
    #008B8B 100%
  );
  box-shadow: 0 8px 25px rgba(0, 206, 209, 0.4);
}

.aboutWord {
  width: 90%;
  max-width: 900px;
  margin-top: 50px;
  margin-bottom: 50px;
  padding: 40px;
  background-color: var(--card-bg);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  color: var(--text-secondary);
  border: 1px solid rgba(255, 215, 0, 0.1);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #333;
  line-height: 1.8;
}

.aboutWord h1,
.aboutWord h2,
.aboutWord h3 {
  margin-top: 25px;
  font-weight: 700;
  color: var(--accent-gold);
}

.aboutWord h1 {
  font-size: 2.2rem;
  margin-bottom: 10px;
}

.aboutWord h2 {
  font-size: 1.6rem;
  margin-bottom: 10px;
}

.aboutWord h3 {
  font-size: 1.2rem;
  margin-right: 8px;
}

.aboutWord p {
  font-size: 1rem;
  margin-bottom: 10px;
  color: var(--text-secondary);
}

.aboutWord ol,
.aboutWord ul {
