.btn1signup {
  height: 45px;
  width: 100%;
  max-width: 500px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, var(--accent-gold) 0%, var(--accent-gold-dark) 100%);
  color: var(--primary-dark);
  border: none;
  border-radius: 10px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(255, 215, 0, 0.3);
  position: relative;
  overflow: hidden;
  z-index: 1;
  margin: 0.5rem 0;
}

.btn1signup::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, var(--accent-teal) 0%, #008B8B 100%);
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: -1;
}

.btn1signup:hover::before {
  opacity: 1;
}

.btn1signup:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(255, 215, 0, 0.4);
}

.btn1signup p {
  margin: 0 10px;
}

