  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, var(--accent-teal) 0%, #008B8B 100%);
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: -1;
}

.linksignup:hover::before {
  opacity: 1;
}

.linksignup:hover {
  transform: translateY(-3px);
