}

.linesignup {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 500px;
  margin: 1rem 0;
}

.linesignup div {
  flex: 1;
  height: 1px;
  background-color: var(--border-color);
}

.linesignup p {
  color: var(--text-secondary);
  padding: 0 1rem;
  font-size: 0.9rem;
}

.secondsignup > p {
  color: var(--text-secondary);
  margin-top: 1rem;
  font-size: 0.9rem;
}

.secondsignup > p a {
  color: var(--accent-gold);
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s ease;
}

.secondsignup > p a:hover {
  color: var(--accent-teal);
}

.fourthsignup select{
  height: 45px;
  width: 100%;
  max-width: 500px;
  padding: 0 1.2rem;
  outline: none;
  border: 1px solid var(--border-color);
  border-radius: 10px;
  background-color: var(--secondary-dark);
  color: var(--text-primary);
  font-family: inherit;
  font-size: 1rem;
  transition: all 0.3s ease;
  align-self: center;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .innersignup {
    width: 95%;
    padding: 1.5rem;
  }
  
  .inputsignup,
  .btn1signup {
    width: 100%;
  }
  
  .linesignup div {
    width: 100px;
  }
}
@media (max-width:760px) {
  .center img {
    max-width: 200px;
  }

  h1 {
    font-size: 1.8rem;
  }  
}

