/* Knowledge Page - Akk Hustlers Premium Dark Theme */
.Knowlegdeinput-section {
  background: var(--card-bg);
  border-radius: 16px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.2);
  padding: 2rem;
  margin: 2rem auto;
  width: 100%;
  max-width: 1000px;
  border: 1px solid rgba(255, 215, 0, 0.1);
  transition: all 0.3s ease;
}

.Knowlegdeinput-section:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 35px rgba(0, 0, 0, 0.3);
}

.Knowlegdetitle {
  font-size: 1.8rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
  color: var(--accent-gold);
  position: relative;
}

.Knowlegdetitle::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 0;
  width: 60px;
  height: 3px;
  background-color: var(--accent-teal);
}

.Knowlegdedescription {
  color: var(--text-secondary);
  margin-bottom: 1.5rem;
  font-size: 1.1rem;
  line-height: 1.6;
}

#topicsInput {
  width: 100%;
  min-height: 150px;
  padding: 1.2rem;
  border: 1px solid var(--border-color);
  border-radius: 8px;
  background-color: var(--secondary-dark);
  color: var(--text-primary);
  font-family: inherit;
  font-size: 1rem;
  resize: vertical;
  transition: all 0.3s ease;
}

#topicsInput:focus {
  outline: none;
  border-color: var(--accent-gold);
  box-shadow: 0 0 0 2px rgba(255, 215, 0, 0.2);
}

.Knowledgebtn {
  background: linear-gradient(
    135deg,
    var(--accent-gold) 0%,
    var(--accent-gold-dark) 100%
  );
  color: var(--primary-dark);
  border: none;
  border-radius: 10px;
  padding: 0.9rem 1.8rem;
  font-size: 1rem;
  font-weight: 600;
  transition: all 0.3s ease;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin: 10px;
  box-shadow: 0 4px 15px rgba(255, 215, 0, 0.3);
  position: relative;
  overflow: hidden;
  z-index: 1;
}
.point{
  cursor: pointer;
}
.nopoint{
  cursor: not-allowed;
}

.Knowledgebtn::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    135deg,
    var(--accent-teal) 0%,
    #008B8B 100%
  );
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: -1;
}

.Knowledgebtn:hover::before {
  opacity: 1;
}

.Knowledgebtn:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(255, 215, 0, 0.4);
}

.outerspin {
  height: 50px;
  width: 100px;
  background: linear-gradient(
    135deg,
    var(--accent-gold) 0%,
    var(--accent-gold-dark) 100%
  );
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px;
  border-radius: 10px;
  padding: 0.75rem 1.5rem;
  box-shadow: 0 4px 15px rgba(255, 215, 0, 0.3);
  font-size: 0.95rem;
  font-weight: 500;
}

.spinner {
  height: 30px;
  width: 30px;
  border-top: 3px solid var(--primary-dark);
  border-right: 3px solid var(--primary-dark);
  animation: spin 1s infinite;
  border-radius: 50%;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(13, 13, 13, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
}

.modal-content {
  background: var(--card-bg);
  border-radius: 16px;
  box-shadow: 0 8px 40px rgba(0, 0, 0, 0.3);
  width: 90%;
  max-width: 700px;
  max-height: 90vh;
  overflow-y: auto;
  border: 1px solid rgba(255, 215, 0, 0.1);
}

.modal-header {
  padding: 1.5rem;
  border-bottom: 1px solid var(--border-color);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.modal-title {
  font-size: 1.5rem;
  font-weight: 600;
  color: var(--accent-teal);
}

.close-btn {
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  color: var(--text-secondary);
  transition: color 0.3s ease;
}

.close-btn:hover {
  color: var(--accent-gold);
}

.modal-body {
  padding: 1.5rem;
}

.question-counter {
  color: var(--accent-gold);
  font-weight: 500;
  margin-bottom: 1rem;
  font-size: 1.1rem;
}

.question {
  font-size: 1.2rem;
  font-weight: 500;
  margin-bottom: 1.5rem;
  line-height: 1.6;
  color: var(--text-primary);
}

.options {
  display: grid;
  gap: 1rem;
  margin-bottom: 2rem;
}

.option {
  padding: 1.2rem;
  border: 1px solid var(--border-color);
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  background-color: var(--secondary-dark);
  color: var(--text-secondary);
}

.option:hover {
  border-color: var(--accent-teal);
  background-color: rgba(0, 206, 209, 0.1);
  color: var(--text-primary);
}

.selected {
  background-color: rgba(255, 215, 0, 0.1);
  border-color: var(--accent-gold);
  color: var(--accent-gold);
  font-weight: 500;
}

.navigation {
  display: flex;
  justify-content: space-between;
  padding-top: 1.5rem;
  border-top: 1px solid var(--border-color);
}

.prevfullbtn, .prevfullbtnnew {
  height: 45px;
  width: 110px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--secondary-dark);
  border: 1px solid var(--border-color);
  border-radius: 10px;
  font-size: 1rem;
  font-weight: 500;
  color: var(--text-secondary);
  cursor: pointer;
  transition: all 0.3s ease;
}

.prevfullbtn:hover {
  background-color: rgba(255, 215, 0, 0.1);
  border-color: var(--accent-gold);
  color: var(--accent-gold);
}

.nextfullbtn, .nextfullbtnnew {
  height: 45px;
  width: 110px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(
    135deg,
    var(--accent-gold) 0%,
    var(--accent-gold-dark) 100%
  );
  border: none;
  border-radius: 10px;
  color: var(--primary-dark);
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(255, 215, 0, 0.3);
}

.nextfullbtn:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(255, 215, 0, 0.4);
}

.prevfullbtnnew, .nextfullbtnnew {
  cursor: not-allowed;
  opacity: 0.7;
}

.results {
  text-align: center;
  padding: 2rem;
}

.results-title {
  font-size: 1.8rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
  color: var(--accent-gold);
}

.score {
  font-size: 3rem;
  font-weight: 700;
  margin: 2rem 0;
  color: var(--text-primary);
  background: linear-gradient(
    135deg,
    var(--accent-gold) 0%,
    var(--accent-teal) 100%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.feedback {
  background-color: var(--secondary-dark);
  padding: 1.8rem;
  border-radius: 15px;
  margin-bottom: 2rem;
  border: 1px solid rgba(255, 215, 0, 0.1);
}

.redDes {
  color: #ff6b6b;
}

.YellowDes {
  color: var(--accent-gold);
}

.GreenDes {
  color: #51cf66;
}

.blogs-page {
  margin-top: 110px;
  padding: 2rem;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.blogs-title {
  font-size: 2rem;
  font-weight: 700;
  color: var(--accent-gold);
  text-align: center;
  position: relative;
  margin-bottom: 2rem;
}

.blogs-title::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 3px;
  background-color: var(--accent-teal);
}

.blogs-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2rem;
}

.sub-blog-card {
  background: var(--card-bg);
  border-radius: 16px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  border: 1px solid rgba(255, 215, 0, 0.1);
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
}

.sub-blog-card:hover {
  transform: translateY(-8px);
  box-shadow:
    0 1px 2px #00ffff,
