.qr-wrapper {
  background-color: var(--primary-dark);
  color: var(--text-primary);
  min-height: 100vh;
  padding: 3rem 1.5rem;
  font-family: 'Inter', sans-serif;
  margin-top: 110px;
}

.qr-container {
  background-color: var(--card-bg);
  padding: 2rem;
  max-width: 900px;
  margin: auto;
  border-radius: 1rem;
  box-shadow: 0 0 40px rgba(0, 206, 209, 0.15);
}

.qr-title {
  font-size: 2.25rem;
  font-weight: bold;
  color: var(--accent-gold);
  margin-bottom: 1.5rem;
}

.qr-intro,
